@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);

html {
  height: 100vh;
}
body {
  background-image: linear-gradient(#489ab1, #e6f5f9);
  background-repeat: no-repeat;
  background-size: cover;
}
.App {
  text-align: center;
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
@media screen and (max-width: 400px) {
  html {
    width: 100%;
    height: 100%;
  }
  body {
    background-image: linear-gradient(#489ab1, #e6f5f9);
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.input {
  border-radius: 4px;
}
.button {
  border-radius: 4px;
  background-color: transparent;
  padding: 5px;
  margin: 5px;
  color: green;
  border: green 1px solid;
}
.button:hover {
  /* background-color: black; */
  color: white;
  border-color: white;
}
@media screen and(max-width: 400px) {
  .form {
    display: -webkit-flex;
    display: flex;
  }
}

.logo {
  color: white;
  width: 3em;
  height: 3em;
}
.navContainer {
  display: -webkit-flex;
  display: flex;
  background-color: rgb(80, 77, 77);
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  margin-bottom: 3%;
}
.leftSide {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}
.leftSide div {
  margin: 10px;
}
.navLinks {
  color: white;
  border-bottom: rgb(80, 77, 77) 3px solid;
}
.navLinks:hover {
  text-decoration: none;
  color: white;
  border-bottom: white 3px solid;
}
.title {
  font-family: "Fjalla One";
  font-size: 1.5em;
}

.about {
  font-family: "Fjalla One";
}
@media screen and (max-width: 400px) {
  .navContainer {
    background-color: rgb(80, 77, 77);
  }
  .title {
    display: none;
  }
}

.aboutTitle {
  font-family: "Fjalla One";
  border-bottom: 1px black solid;
  margin-bottom: 3%;
}
.aboutContainer {
  width: 50%;
  border: 1px white solid;
  border-radius: 2em;
  padding: 2%;
}

.cards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.card {
  margin: 20px;
  width: 18rem;
}

.card-title {
  margin-bottom: 15px;
}

#closeIcon {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
}

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;
}

.cityContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.ciudad {
  border: 1px rgb(255, 255, 255) solid;
  padding: 2%;
  border-radius: 5%;
  -webkit-backdrop-filter: blur(7px);
          backdrop-filter: blur(7px);
}
.data {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}
.titleCity {
  margin-bottom: 5%;
  font-family: "Fjalla One";
}

